import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class OpenFoodFactsService {

  constructor(private http: HttpClient) { }

  searchItems(searchString: string, apiCallback){
	this.http.get('https://world.openfoodfacts.org/cgi/search.pl?search_simple=1&action=process&json=1&search_terms=' + searchString).subscribe( data => {
    	apiCallback(data);
    });
  }
}
